.archives-outer-container {
  // background: url(http://synthetic-turf.com/wp-content/uploads/2014/04/Augusta-National-HD-Wallpaper.jpg);
  background-color: $LightGrey;
  opacity: 1;
  z-index: -1;
}

.archives-container {
  width: 90%;
  max-width: 900px;
  padding-bottom: 80px;
  margin: 0px auto;
  z-index: 1;

  h1 {
    margin-top: 0px;
    padding-top: 15px;
    color: $Navy;
    font-size: 3.6em;
  }

  .newsletter-block {
    @include box-shadow($box-shadows);
    background-color: white;
    margin-bottom: 80px;
    padding: 20px;
    border: 3px solid $Navy;
    border-radius: 5px;

    &:hover {
      cursor: pointer;
      background-color: $Navy;
      color: white;

    }

    .newsletter-media {
      display: inline-block;
      width: 40%;
      overflow: hidden;
      vertical-align: top;
      // @include box-shadow($light-box-shadows);
      max-height: 350px;

      img {
        display: block;
        margin: 12px auto;
        width: 100%;
      }

    }

    .newsletter-preview {
      display: inline-block;
      width: 58%;
      max-height: 350px;
      overflow-y: scroll;

      h2 {
        margin-top: 0px;
        margin-bottom: 0px;
        vertical-align: top;
        padding-top: 0px;
        padding-bottom: 0px;
        font-size: 3.0em;
        white-space: nowrap;
      }

      p {
        margin-top: 3px;
        overflow: hidden;
        text-overflow-multiline: ellipsis;
        text-overflow: '...';
        font-size: 2.4em;
        line-height: 1.8em;
      }
    }

    h2, h4 {
      @include padding ($standard-text-padding);
      @include margin ($standard-text-margin);
      font-weight: bold;
    }

    .newsletter-date {
      color: $BrightBlue;
      text-align: left;
      font-size: 2.2em;
      margin: 0px auto;
      margin-bottom: 10px;
      padding: 0px;
      font-weight: bold;
    }


    p {
      @include padding ($standard-text-padding);
      @include margin ($standard-text-margin)
    }


    .newsletter-subjects {
      list-style-type: none;
      @include padding ($standard-text-padding);
      padding: 10px 0px;
      margin: 0px auto;

      span {
        padding-left: 5px;
        padding-right: 10px;
      }

      li {
        display: inline-block;
        margin: 10px;
        margin-left: 0px;

        button {
          background-color: $LightGrey;
          color: $Navy;
          padding: 15px;
          font-size: 1.0em;
          font-weight: bold;
          border: none;
          text-align: center;
          // @include box-shadow($light-box-shadows);

          &:hover {
            background-color: $BrightBlue;
            color: white;
          }
        }
      }
    }

    .read-more-newsletter {
      text-align: right;
      font-size: 1.1em;
      margin: 0px;
      padding: 0px;
      padding-right: 5px;
      text-align: right;

      a {
        color: $BrightBlue;
        font-weight: bold;
        text-decoration: none;
      }
    }

  }
}

